

.signup-container{
    display: flex;
    align-items: center;
    justify-content: center;
}


.signup-main{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 400px;
    height: 450px;
    margin: 100px 0px;
    background-color: black;
    box-shadow: 0 4px 8px 0 rgba(255, 255, 255, 0.2); 
}

.signup-input{
    width: 200px;
    height: 50px;
    margin: 5px 0px;
    padding: 1px 10px;
    font-size: 0.9rem;
}

.signup-input::placeholder{
    padding: 0px 10px;
}

.signup-checkbox{
    display: flex;
    margin: 10px 0px;
    color: white;
}

.gender{
    margin: 0px 10px;
}
.chekbox-input{
    margin: 0px 5px;
    padding: 1px 10px;
    color: white;
}


.link-content{
    text-decoration: none;
    color: white;
}

.signup-btn-main{
    display: flex;
    margin: 10px 0px;
}
.signup-btn{
    width: 220px;
    height: 50px;
    padding: 0px 5px;
    background-color: #4169E1;
    color: white;
    border: 1px solid #4169E1;
    border-radius: 3px;
    font-size: 0.9rem;
    cursor: pointer;
}
.signup-main h2{
  color: white;
}



/* Home */

.Home-container{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
     overflow: hidden;
}

.home-main{
 display: flex;
 align-items: center;
 justify-content: center;
 height: 100vh;
 background-color: black;
}

.sidebar-home{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 0px;
}


/* sidebar */


.sidebar-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    border-right: 1px solid grey;
   
}


.sidebar-main{
    display: flex;
   
}
.side-input{
    padding: 6px 20px;
    background-color: #333333;
    border: 1px solid #333333;
    border-radius: 8px;
   color: white;

}
.side-input::placeholder{
    color: white;
}
.sidebar-btn-main{
    display: flex;
    align-items: center;
    justify-content: center;
}
.sidebar-btn{
    display: flex;
    padding: 6px 15px;
    margin: 0px 5px;
    background-color: #2874F0;
    border: 1px solid #2874F0;
    color: white;
    border-radius: 3px;
    cursor: pointer;
}

.logout-btn-main{
    display: flex;
    margin: 10px 0px;
    align-items: center;
    justify-content: center;
}

.logout-btn{
    padding: 5px 20px;
    background-color: #2874F0;
    border: 1px solid #2874F0;
    color: white;
    border-radius: 4px;
}


/* otherusers */

.othersusers-container{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px 0px;
    width: 300px;
    height: 50px;
    border-radius: 3px;
    box-shadow: 0 4px 8px 0 rgba(255, 255, 255, 0.2); 
}
.othersusers-container:hover{
    background-color: #060e15;
    cursor: pointer;
}

.online-btn{
     border-radius: 20px;
     margin: 0px 5px;
}

.profile-image{
    display: flex;
    border-radius: 30px;
    height: 50px;
    margin: 0px 10px;
}

.user-profile{
    overflow: auto;
    display: flex;
    flex-direction: column;
    width: 300px;
   height: 500px;
   color: white;
}


.message-container{
    display: flex;
    flex-direction: column;
    width: 405px;
    height: 600px;
}
.message{
    width: 300px;
    margin: 10px 10px;
}
.profile-message{
    display: flex;
    
}
.message-main{
display: flex;
width: 390px;
box-shadow: 0 4px 8px 0 rgba(255, 255, 255, 0.2); 

}
.message-main p{
 margin: 10px 5px;
  color: white;

}


/* sendInput */

.sendmessage-main{
    display: flex;
    position: relative;
     top: 500px;
     position: fixed;
  
}

.send-input{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 350px;
    height: 50px;
    margin: 0px 20px;
    background-color: #060e15;
    border-radius: 8px;
}
.send-input-text{
    display: flex;
    margin: 0px 20px;
    align-items: center;
    justify-content: center;
   height: 30px;
   padding: 20px 20px;
   background-color: #060e15;
   border: 1px solid #060e15;
   color: white;
   border-radius: 3px;
   box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); 
}
.send-input-text::placeholder{
    color: white;
    font-size: 0.9rem;
}

.send-btn-main{
   display: flex;

}
.send-btn{
 font-size: 0.9rem;
 background-color: #060e15;
 color: white;
 padding: 2px 10px;
 border: none;
cursor: pointer;
}

.participants-message{
    width: 400px;
    height: 450px;
    padding: 15px;
    overflow: auto;
}


.message-text{
    color: white;
}

.conversation{
 display: flex;
 margin: 20px 30px;
 color: white;
}


.conversation h3 {
 margin: 0px 10px;
}


@media (max-width: 600px) {
    .sidebar-container {
      width: 300px; 
      height: 100vh;
    }
 
  
  }