@tailwind base;
@tailwind components;
@tailwind utilities;


*{
  margin: 0;
  padding: 0;
}


::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

/* Scroll bar ka background color set kare */
::-webkit-scrollbar-track {
  background: #060e15;
}

/* Scroll bar ka thumb ka color set kare */
::-webkit-scrollbar-thumb {
  background:rgba(255, 255, 255, 0.2);
}

/* Jab user hover kare to scroll bar ka thumb ka color badle */
::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.2);
}